 display: flex;\n    flex-direction: row;\n    align-items: center;\n    justify-content: space-between;\n}\n\n.row {\n    &.full-row {\n        margin-right: 0px;\n        width: 100%;\n    }\n}\n.col-no-padding {\n    padding-left: 0px;\n    padding-right: 0px;\n}\n\n.around-huge-flag {\n    .team_flag {\n        height: 22px;\n    }\n}\n\n.bolded {\n    font-weight: 700 !important;\n}\n.underlined {\n    text-decoration: underline;\n}\n\n.team_flag {\n    border: 1px solid rgba(0, 0, 0, 0.2);\n    height: 15px;\n    &.no-border {\n        border: 0px;\n    }\n    &.larger {\n        height: 20px;\n        width: 30px;\n    }\n}\n\n.dropdown.dropdown-input {\n    ul > li {\n        cursor: pointer;\n        padding: 3px;\n        padding-right: 9px;\n        padding-left: 6px;\n        &:hover {\n            background: #d3d3d3;\n        }\n    }\n}\n@media (max-width: 767px) {\n    .dropdown.dropdown-input {\n        .dropdown-menu {\n            max-height: 280px;\n            overflow-y: scroll;\n        }\n    }\n}\n\n@mixin menu_icons_hover() {\n    div.icon {\n        &.podium_icon {\n            background-image: url('/img/podium_hover.svg');\n        }\n        &.bet_icon {\n            background-image: url('/img/bet_hover.svg');\n        }\n        &.watch_bets_icon {\n            background-image: url('/img/watch_hover.svg');\n        }\n        &.pre_game_icon {\n            background-image: url('/img/pre_game_hover.svg');\n        }\n        &.form_icon {\n            background-image: url('/img/form_hover.svg');\n        }\n        &.change_password_icon {\n            background-image: url('/img/change_password_hover.svg');\n        }\n        &.logout_icon {\n            background-image: url('/img/logout_hover.svg');\n        }\n    }\n}\n\n.navbar-nav {\n    li {\n        > a {\n            > div.icon {\n                width: 30px;\n                height: 30px;\n                position: absolute;\n                top: 6px;\n                right: 5px;\n                background-size: cover;\n                &.podium_icon {\n                    background-image: url('/img/podium.svg');\n                }\n                &.bet_icon {\n                    background-image: url('/img/bet.svg');\n                }\n                &.watch_bets_icon {\n                    background-image: url('/img/watch.svg');\n                }\n                &.pre_game_icon {\n                    background-image: url('/img/pre_game.svg');\n                }\n                &.form_icon {\n                    background-image: url('/img/form.svg');\n                }\n                &.change_password_icon {\n                    height: 29px;\n                    background-image: url('/img/change_password.svg');\n                }\n                &.logout_icon {\n                    background-image: url('/img/logout.svg');\n                }\n            }\n            > span.menu-label {\n                margin-right: 30px;\n            }\n            &:hover {\n                @include menu_icons_hover;\n            }\n        }\n        &.active {\n            @include menu_icons_hover;\n        }\n    }\n}\n\ntable {\n    > tbody {\n        > tr {\n            > td.open-match-teams-cell {\n                padding-right: 0px;\n                padding-left: 0px;\n            }\n        }\n    }\n}\n\n.open-matches-date-header {\n    width: 120px;\n}\n.open-matches-bet-header {\n    width: 100px;\n}\n@media (max-width: 767px) {\n    .open-matches-bet-header {\n        width: 80px;\n    }\n    .open-matches-date-header {\n        width: 60px;\n    }\n    .open-matches-bet-cell {\n        padding-left: 5px;\n    }\n}\n\n.bet-winner-bg {\n    background-color: #86d686ab;\n}\n.bet-loser-bg {\n    background-color: #d66d6d7a;\n}\n.winner_color {\n    color: #00ad00;\n}\n.loser_color {\n    color: red;\n}\n\n@import 'ko_switch';\n@import 'oldStyling';\n@import './styles/global';\n\n\n// override bootstrap css:\n.MuiInputBase-root {\n    legend {\n        width: auto;\n    }\n}\n\n.LB-AppMainWrapper {\n    min-height: 100vh;\n    .LB-TitleText {\n        @include headerText;\n    }\n    .LB-NormalText {\n        @include headerText;\n    }\n    .LB-FloatingFrame {\n        @include floatingFrame;\n        border-radius: 12px;\n        padding: 12px;\n        margin-bottom: 16px;\n    }\n    .LB-CustomTable {\n        @include floatingTable;\n    }\n    .LB-AdminViewContainer {\n        background-color: #eeeeeee3;\n        width: calc(100% + 16px);\n        margin-right: -8px;\n        padding: 30px 12px;\n        border-radius: 24px;\n    }\n}",".ko_switch_input {\n    position: relative;\n    .tw-toggle {\n        display: block;\n        padding: 2px 2px;\n        border-radius: 20px;\n        position: absolute;\n        border: 2px solid #95a5a6;\n        flex-direction: column;\n        margin-top: 0px;\n        left: -7px;\n\n        label {\n            text-align: center;\n            font-family: sans-serif;\n            display: block;\n            color: #95a5a6;\n            position: relative;\n            z-index: 2;\n            margin: 0;\n            padding: 2px 4px;\n            font-size: 15px;\n        }\n\n        input {\n            position: absolute;\n            z-index: 3;\n            opacity: 0;\n            width: 100%;\n            left: 0px;\n            margin-top: 0px;\n            height: 23px;\n            cursor: pointer;\n            &:checked {\n                &[value='home'] ~ span {\n                    background: #286090;\n                    top: 3px;\n                }\n                &[value='away'] ~ span {\n                    background: #286090;\n                    top: 53px;\n                }\n                &[value='-1'] ~ span {\n                    background: #95a5a6;\n                    top: 22px;\n                }\n                + label {\n                    color: #ffff00;\n                }\n            }\n        }\n\n        > span {\n            height: 22px;\n            width: 22px;\n            line-height: 21px;\n            border-radius: 50%;\n            background: #d0dedf;\n            display: block;\n            position: absolute;\n            left: 2px;\n            top: 29px;\n            transition: all 0.3s ease-in-out;\n        }\n    }\n    .arrow-icon {\n        color: #286090;\n    }\n}\n","/* Remove the navbar's default margin-bottom and rounded borders */\n.navbar {\n    margin-bottom: 0;\n    border-radius: 0;\n}\n\n.admin {\n    display: none !important;\n}\n\n* {\n    text-align: right;\n    direction: rtl;\n}\n\n@media (min-width: 768px) {\n    ul.navbar-nav > li {\n        float: right;\n    }\n}\n/* Set height of the grid so .sidenav can be 100% (adjust as needed) */\n// .row.content {height: 450px}\n\n/* Set gray background color and 100% height */\n// .sidenav {\n//     padding-top: 20px;\n//     background-color: #f1f1f1;\n//     height: 100%;\n// }\n\n/* Set black background color, white text and some padding */\nfooter {\n    background-color: #555;\n    color: white;\n    padding: 15px;\n}\n\n/* On small screens, set height to 'auto' for sidenav and grid */\n@media screen and (max-width: 767px) {\n    .sidenav {\n        height: auto;\n        padding: 15px;\n    }\n    .row.content {\n        height: auto;\n    }\n}\n\n// .rank-1 { background-color: #1c7430!important; color: #ffffff!important; }\n// .rank-2 { background-color: #1f3e1a!important; color: #ffffff!important; }\n// .rank-3 { background-color: #495057!important; color: #ffffff!important; }\n// .rank-4 { background-color: #645b62!important; color: #ffffff!important; }\n",".flag-icon {\n    max-height: 15px;\n    margin: 0 10px;\n}\n\n.dash-space {\n    margin: 0 10px;\n}\n\n#toast-container {\n    padding-top: 75px;\n}","@import './shadows';\n\n\n@mixin centerContainer {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@mixin flexRow {\n    display: flex;\n    align-items: center;\n}\n\n@mixin fullView {\n    height: 100%;\n    width: 100%;\n}\n\n@mixin floatingFrame {\n    box-shadow: $basic-shadow;\n    background-color: rgba(255,255,255, 0.95);\n}\n\n@mixin headerText {\n    text-shadow: -3px 2px 8px black;\n    color: white;\n}\n\n@mixin floatingTable {\n    background-color: rgb(255 255 255 / 95%);\n    border-radius: 12px;\n}","$basic-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;\n$medium-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;"],"sourceRoot":""}]);
