  }\n  }\n}\n\n@include themable_app_header(tournament-theme, $tournament_1);\n@include themable_app_header(tournament-theme-1, $tournament_1);\n@include themable_app_header(tournament-theme-2, $tournament_2);\n@include themable_app_header(tournament-theme-3, $tournament_3);\n@include themable_app_header(tournament-theme-4, $tournament_4);\n@include themable_app_header(tournament-theme-5, $tournament_5);","@import './shadows';\n\n\n@mixin centerContainer {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@mixin flexRow {\n    display: flex;\n    align-items: center;\n}\n\n@mixin fullView {\n    height: 100%;\n    width: 100%;\n}\n\n@mixin floatingFrame {\n    box-shadow: $basic-shadow;\n    background-color: rgba(255,255,255, 0.95);\n}\n\n@mixin headerText {\n    text-shadow: -3px 2px 8px black;\n    color: white;\n}\n\n@mixin floatingTable {\n    background-color: rgb(255 255 255 / 95%);\n    border-radius: 12px;\n}","$basic-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;\n$medium-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;","@import '../styles/colors';\n@import '../styles/mixins';\n@import '../styles/shadows';\n@import '../styles/themes/tournaments';\n\n\n@mixin tournament_link($tournament_theme){\n  background: map_get($tournament_theme, main);\n  &:hover {\n    background: map_get($tournament_theme, gradient);\n  }\n}\n\n\n.LigaBet-PopupMenu-root {\n  .MuiMenu-paper {\n    background-color: rgba(255,255,255,0.9);\n  }\n  .LigaBet-PopupMenu-list {\n    &.tournamentsMenu {\n      .TournamentMenu-CompetitionTitle{\n        font-size: 15px;\n        text-align: center;\n        text-decoration: underline;\n        padding: 8px;\n        padding-bottom: 0px;\n        cursor: default;\n        background-color: #9e54eb3d;\n      }\n      .LigaBet-TournamentItemLink {\n        border-radius: 2px;\n        color: white;\n        padding: 6px 14px;\n        padding-left: 24px;\n        width: 100%;\n        @include tournament_link($tournament_1);\n        &:nth-of-type(1) {\n          @include tournament_link($tournament_1);\n        }\n        &:nth-of-type(2) {\n          @include tournament_link($tournament_2);\n        }\n        &:nth-of-type(3) {\n          @include tournament_link($tournament_3);\n        }\n        &:nth-of-type(4) {\n          @include tournament_link($tournament_4);\n        }\n        &:nth-of-type(5) {\n          @include tournament_link($tournament_5);\n        }\n        &.selectedTournament {\n          border: 3px solid rgba(0,0,0,0.45);\n          .wrapper {\n            .MuiBadge-root {\n              top: 2px;\n              left: 2px;\n            }\n          }\n        }\n        &:not(.selectedTournament) {\n          .wrapper {\n            padding: 3px;\n          }\n        }\n        .wrapper {\n          @include fullView;\n          .tournamentRow {\n            @include flexRow;\n            padding-right: 4px;\n            font-size: 18px;\n            .tournamentName {\n              z-index: 10;\n            }\n          }\n          .MuiBadge-root {\n            position: absolute;\n            top: 5px;\n            left: 5px;\n            .MuiBadge-badge {\n              transform: none;\n            }\n          }\n          .utlRow {\n            display: flex;\n            align-items: flex-end;\n            font-size: 14px;\n            margin-top: 6px;\n            .utlName {\n              margin-right: 4px;\n            }\n          }\n        }\n      }\n      .buttonLink {\n        background-color: #9f9f9f57;\n        padding: 8px;\n        @include centerContainer;\n      }\n      .changeLiveMode{\n        @include centerContainer;\n        cursor: pointer;\n        background-color: #c7768985;\n        width: 100%;\n        border-radius: 10px;\n        padding: 5px;\n        margin-bottom: 12px;\n        transition: background-color 0.4s;\n        &:hover {\n          background-color: #b5385585;\n        }\n        > svg {\n          margin-left: 8px;\n        }\n      }\n    }\n  }\n}",".LigaBet-LinkMenuItem {\n  &.MuiMenuItem-root {\n    min-height: 0;\n    padding: 8px 16px;\n    color: white;\n    font-size: 16px;\n    justify-content: flex-start;\n    .MuiBadge-root {\n      .MuiBadge-badge {\n        @media (min-width: 900px){\n          transform: translateY(-50%) translateX(-50%);\n          z-index: -1;\n        }\n        @media (max-width: 900px){\n          top: 10px;\n          left: -20px;\n        }\n      }\n    }\n  }\n}"],"sourceRoot":""}]);
